import styled from 'styled-components';

const Title = styled.h3`
  margin: 0 1rem;
  text-align: center;
  /* transform: skew(-5deg) rotate(-1deg); */
  margin-top: -3rem;
  text-shadow: 3px 3px 0 rgba(0, 0, 0, 0.2);
  a {
    background: ${props => props.theme.red};
    display: inline;
    line-height: 1.3;
    font-size: 4rem;
    text-align: center;
    font-weight:500;
    color: white;
    padding: 0 1rem;
    border-radius:50%;
  }
`;

export default Title;
